.content-bar {
    width: 356px;
    height: 548px;
    padding-right: 4px;
    overflow-y: auto;
    overflow-x: hidden;
    &:hover {
        &::-webkit-scrollbar-thumb {
            background: #d7d7d7; 
        }
    }
    &::-webkit-scrollbar {
        width: 6px;
      }
      
      /* Track */
      &::-webkit-scrollbar-track {
        border-radius: 10px;
      }
       
      /* Handle */
      &::-webkit-scrollbar-thumb {
        background: transparent; 
        border-radius: 10px;
      }
      
      /* Handle on hover */
      &::-webkit-scrollbar-thumb:hover {
        background: #d0cccc; 
      }
    .content-bar-option {
        margin-top: 16px;
        padding-left: 12px;
        .content-bar-link {
            display: block;
            padding: 8px;
            height: 32px;
            line-height: 32px;
            font-size: 18px;
            color: #333;
            text-decoration: none;
            font-weight: bold;
            border-radius: 4px;
            &.active {
                color: #fe2c55;
            }
            &:hover {
                background-color: #f8f8f8;
            }
            .wrapper-link {
                display: flex;
                align-items: center;
                .icon-link {
                    width: 32px;
                    height: 24px;
                    font-size: 24px;
                    text-align: center;
                    line-height: 24px;
                    margin-right: 8px;
                }
            }
        }
    }
    .line {
        width: 95%;
        height: 1px;
        margin: 8px;
        background-color: #e4e4e5;
        margin-left: 20px;
    }
    .content-bar-login {
        margin-left: 16px;
        margin-bottom: 24px;
        .contents-text {
            color: #8d8e94;
        }
        .content-bar-btn {
            width: 100%;
            border: 1px solid #fe6180;
            padding: 16px 0;
            border-radius: 4px;
            font-size: 17px;
            font-weight: bold;
            color: #fe2c55;
            background-color: #fff;
            &:hover {
                background-color: #fff3f5;
                cursor: pointer;
            }
        }
    }
    .content-bar-following {
        margin-left: 16px;
        .content-bar-following-heading {
            margin: 12px 0;
            font-size: 14px;
            font-weight: 500;
            color: #575961;
        }
        .content-bar-account {
            .content-bar-account-link {
                text-decoration: none;
                display: block;
                padding: 8px;
                &:hover {
                    background-color: #f8f8f8;
                }
                .content-bar-wrapper-account {
                    display: flex;
                    align-items: center;
                    .content-bar-img {
                        width: 32px;
                        height: 32px;
                        border-radius: 50%;
                        margin-right: 12px;
                    }
                    .content-bar-des {
                        .content-bar-name-account {
                            font-size: 15px;
                            margin: 0;
                            color: #333;
                        }
                        .content-bar-name-text {
                            font-size: 14px;
                            margin: 0;
                            margin-top: -3px;
                            color: #919297;
                        }
                    }
                }
            }
        }
        .content-view-all {
            font-size: 15px;
            margin: 8px 0 16px 8px;
            font-weight: 500;
            color: #fe2c55;
            &:hover {
                cursor: pointer;
            }
        }
        .line {
            margin-left: 8px;
        }
        .content-bar-discover {
            .content-bar-discover-container {
                .content-bar-discover-item {
                    display: flex;
                    align-items: center;
                    .content-bar-item-text {
                        text-decoration: none;
                        color: #7a7c82;
                        display: flex;
                        align-items: center;
                        padding: 0 4px;
                        border: 1px solid #ccc;
                        border-radius: 20px;
                        margin-right: 8px;
                        margin-bottom: 12px;
                        overflow: hidden;
                        &:hover {
                            background-color: #f8f8f8;
                        }
                        .content-bar-item-music {
                            color: #333;
                            display: block;
                            width: 26px;
                            height: 26px;
                            font-size: 18px;
                            text-align: center;
                            line-height: 26px;
                        }
                        .content-bar-item-text-name {
                            display: -webkit-box;
                            -webkit-line-clamp: 1;  /* số dòng hiển thị */
                            -webkit-box-orient: vertical;
                            height: 26px;
                            line-height: 24px;
                            margin: 0;
                            padding-right: 4px;
                        }
                    }
                }
            }
            .line {
                width: 100%;
            }
        }
    }
    .content-bar-footer {
        font-size: 13px;
        color: #a0a1a6;
        font-weight: 500;
        margin-left: 16px;
        margin-top: 20px;
        padding-bottom: 32px;
        .content-bar-footer-wrapper {
            line-height: 1.6;
            .content-bar-footer-link {
                color: #a0a1a6;
                font-weight: 500;
                text-decoration: none;
                margin-right: 6px;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
        .more {
            margin-bottom: 14px;
            &:hover {
                cursor: pointer;
            }
        }
    }
}
.block {
    display: block;
}
.in-block {
    display: inline-block;
}
@media (max-width: 1023px) {
    .content-bar {
        width: 74px;
        padding-right: 0;
        position: fixed;
        box-shadow: 0 0 4px rgba(0, 0, 0, 0.3);
        left: 0;
        .content-bar-option {
            padding-left: 0;
            .content-bar-link {
                padding: 12px 0;
                margin: 0 8px;
                height: unset;
                .wrapper-link {
                    justify-content: center;
                    .icon-link {
                        margin-right: 0;
                    }
                }
            }
        }
        .line {
            width: unset;
            margin: 8px 14px;
        }
        .content-bar-following {
            margin-left: 0;
            .content-bar-account {
                .content-bar-account-link {
                    padding: 12px 0;
                    margin: 0 8px;
                    border-radius: 4px;
                    .content-bar-wrapper-account {
                        justify-content: center;
                        .content-bar-img {
                            margin-right: 0;
                        }
                    }
                }
            }
        }
    }
    .content-video {
        flex: 1;
    }
    
    .wrapper-link {
        justify-content: center;
    }
    .icon-link {
        margin-right: 0;
    }
    .link-text {
        display: none;
    }
    .content-bar-login {
        display: none;
    }
    .line.mobile {
        display: none;
    }
    .content-bar-following-heading {
        display: none;
    }
    .content-bar-des {
        display: none;
    }
    .content-view-all {
        display: none;
    }
    .content-bar-discover {
        display: none;
    }
    .content-bar-footer {
        display: none;
    }
}
