.login {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 2;
    .login-container {
        margin: auto;
        width: 472px;
        height: 545px;
        border-radius: 8px;
        background-color: #ffffff;
        animation: growth ease 0.3s;
        .login-wrapper {
            margin: 48px 48px 12px 48px;
            .login-header {
                display: flex;
                align-items: center;
                justify-content: center;
                .login-close {
                    font-size: 20px;
                    position: relative;
                    top: -28px;
                    right: -68px;
                    width: 40px;
                    height: 40px;
                    text-align: center;
                    line-height: 42px;
                    border-radius: 50%;
                    background-color: #f1f1f2;
                    &:hover {
                        cursor: pointer;
                        background-color: #e7e7e9;
                    }
                }
                .login-title {
                    margin: 0;
                    font-size: 24px;
                    position: relative;
                    left: 28px;
                }
            }
            .login-content {
                margin: 28px 24px 18px 28px;
                height: 352px;
                overflow-y: auto;
                .login-link {
                    text-decoration: none;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    margin-bottom: 12px;
                    .login-icon {
                        font-size: 24px;
                        height: 42px;
                        text-align: center;
                        line-height: 42px;
                        width: 42px;
                        border: 1px solid #e4e4e5;
                        &.black-color {
                            color: #333;
                        }
                        &.fb-color {
                            color: #1877f2;
                        }
                        &.twitter-color {
                            color: #55acee;
                        }
                        &.line-color {
                            color: #3ace01;
                        }
                        &.KakaoTalk-color {
                            color: #ffe812;
                        }
                        &.ig-color {
                            color: #d71f65;
                        }
                    }

                    span.login-text {
                        flex: 1;
                        height: 42px;
                        line-height: 42px;
                        text-align: center;
                        font-size: 16px;
                        color: #333;
                        font-weight: 500;
                        border: 1px solid #e4e4e5;
                        border-left: none;
                    }
                }
            }
        }
        .line {
            width: 100%;
            height: 1px;
            background-color: #e4e4e5;
        }
        .login-footer {
            text-align: center;
            .footer-link {
                text-decoration: none;
                color: #fe2c55;
                font-weight: 500;
                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}
@keyframes growth {
    0% {
        transform: scale(0.5);
    }
    100% {
        transform: scale(1);
    }
}
@media (max-width: 1023px) {
    .login {
        .login-container {
            height: 454px;
            .login-wrapper {
                .login-content {
                    height: 266px;
                }
            }
        }
    }
}
