.header {
    height: 60px;
    background-color: #fff;
    padding: 0 60px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    .header-nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
        padding: 0 28px;
        .header-logo-link {
            text-decoration: none;
            color: #000;
            font-size: 30px;
            font-weight: bold;
            display: flex;
            justify-content: center;
            align-items: center;
            .header-logo {
                width: 28px;
                height: 28px;
            }
        }
        .header-search {
            width: 360px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 25px;
            overflow: hidden;
            position: relative;
            &::after {
                content: '';
                position: absolute;
                right: 52px;
                width: 1px;
                height: 60%;
                background-color: #dddddf;
            }
            .input-search {
                border: none;
                outline: none;
                background-color: #f1f1f2;
                width: 100%;
                height: 32px;
                font-size: 16px;
                padding: 6px 0 6px 16px;
                caret-color: red;
            }
            .link-search {
                display: block;
                text-decoration: none;
                padding: 8px 16px;
                font-size: 21px;
                color: #a8a8ac;
                background-color: #f1f1f2;
                &:hover {
                    background-color: #eaeaeb;
                }
            }
        }
        .config-header {
            display: flex;
            align-items: center;
            justify-content: center;
            .link-config {
                text-decoration: none;
                color: #000;
                font-size: 16px;
                margin-right: 16px;
                font-weight: bold;
                &:hover {
                    text-decoration: underline;
                }
            }
            .btn-config {
                border: none;
                padding: 10px;
                font-size: 16px;
                font-weight: bold;
                color: #ffffff;
                background-color: #fe2c55;
                border-radius: 6px;
                margin-right: 16px;
                &:hover {
                    cursor: pointer;
                }
            }
            .span-config {
                font-size: 20px;
                font-weight: 400;
                position: relative;
                &:hover {
                    cursor: pointer;
                }
                &:hover .children-span-config {
                    display: block;
                }
                .children-span-config {
                    position: absolute;
                    top: 36px;
                    right: -16px;
                    width: 232px;
                    padding: 8px 0;
                    box-shadow: 0 0px 10px 0px #d7d7d7;
                    border-radius: 6px;
                    background-color: #fff;
                    z-index: 10;
                    display: none;
                    &::after {
                        content: '';
                        position: absolute;
                        right: 6px;
                        top: -10px;
                        width: 0;
                        height: 0;
                        border-left: 20px solid transparent;
                        border-right: 20px solid transparent;
                        border-bottom: 20px solid #fff;
                    }
                    .link-children {
                        display: flex;
                        align-items: center;
                        text-decoration: none;
                        font-size: 16px;
                        padding: 8px 0;
                        color: #000;
                        font-weight: bold;
                        &:hover {
                            background-color: #f9f6f6;
                        }
                        .icon-header {
                            display: flex;
                            margin: 0 8px;
                            padding: 4px;
                            font-size: 20px;
                        }
                    }
                }
            }
        }
    }
}
@media (max-width: 1023px) {
    .header {
        padding: 0;
    }
}
@media (max-width: 739px) {
    .header {
        .header-nav {
            .header-search {
                display: none;
            }
        }
    }
}
