.download {
    position: fixed;
    right: 24px;
    bottom: 12px;
    display: flex;
    z-index: 2;
    .download-app-container {
        text-align: right;
        transform: translateY(60%);
        .des-app {
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 14px;
            font-weight: bold;
            border-radius: 20px;
            margin: 0;
            padding: 2px 8px 6px 8px;
            background-color: #ffffff;
            color: #50525a;
            border: 1px solid #d9d9db;
            &:hover {
                cursor: pointer;
                background-color: #f8f8f8;
            }
        }
        .des-app-gototop {
            margin: 14px 0 0 0;
            text-align: center;
            width: 32px;
            height: 32px;
            font-size: 18px;
            line-height: 35px;
            border-radius: 50%;
            align-items: center;
            color: #fff;
            display: inline-block;
            background-color: #fe2c55;
            &:hover {
                cursor: pointer;
                background-color: #ef2a50;
            }
        }
    }
    .download-app-children {
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        background-color: rgba(0, 0, 0, 0.5);
        display: none;
        .download-app-children-container {
            position: relative;
            width: 719px;
            height: 486px;
            margin: auto;
            background-color: #ffffff;
            border-radius: 8px;
            animation: growth ease 0.2s;
            .download-app-header {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 24px 0;
                border-bottom: 1px solid #d7d7d9;
                .download-app-title {
                    margin: 0;
                    font-size: 24px;
                    margin-left: 24px;
                }
                .close-download-app {
                    margin-right: 24px;
                    font-size: 22px;
                    &:hover {
                        cursor: pointer;
                    }
                }
            }
            .download-app-content {
                display: flex;
                .download-app-content-qr {
                    width: 324px;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    .qr-title {
                        text-align: center;
                        margin: 32px 0 40px;
                        font-size: 18px;
                        width: 260px;
                        line-height: 1.5;
                    }
                    .img-qr {
                        width: 200px;
                        height: 200px;
                    }
                }
                .space-down-app {
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    padding: 48px 0;
                    .space-option {
                        margin: 8px 0 10px;
                    }
                    .space-Y {
                        width: 1px;
                        height: 140px;
                        background-color: rgba(22, 24, 35, 0.2);
                    }
                }
                .download-app-content-down {
                    margin: 32px 24px;
                    flex: 1;
                    .download-images {
                        .title-images {
                            margin: 0 0 16px 0;
                            font-size: 18px;
                            text-align: center;
                        }
                        .down-image {
                            width: 288px;
                            display: flex;
                            justify-content: space-between;
                            margin: auto;
                            .image-microsoft {
                                width: 138px;
                                height: 40px;
                                border-radius: 4px;
                            }
                        }
                        .down-image.mt-4 {
                            margin-top: 4px;
                        }
                    }
                    .download-phonenumber {
                        .title-phonenumber {
                            margin: 20px 0 16px;
                            font-size: 18px;
                            text-align: center;
                        }
                        .down-phonenumber {
                            display: flex;
                            justify-content: center;
                            align-items: center;
                            width: 288px;
                            margin: auto;
                            height: 34px;
                            border: 1px solid #d7d7e2;
                            border-radius: 4px;
                            overflow: hidden;
                            .label-phonenumber {
                                height: 34px;
                                text-align: center;
                                width: 77px;
                                line-height: 34px;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                .tip-css {
                                    color: #ccc;
                                    margin-right: -6px;
                                    margin-left: 4px;
                                    margin-top: -4px;
                                }
                            }
                            .input-phonenumber {
                                width: 100%;
                                border: none;
                                outline: none;
                                height: 100%;
                                font-size: 15px;
                                padding-left: 16px;
                                flex: 1;
                                caret-color: red;
                            }
                        }
                    }
                    .footer-down {
                        text-align: center;
                        .btn-footer-down {
                            width: 288px;
                            min-height: 36px;
                            min-width: 164px;
                            border-radius: 4px;
                            border: none;
                            outline: none;
                            font-size: 16px;
                            background-color: #fe2c55;
                            color: #fff;
                            font-weight: bold;
                            margin-top: 16px;
                            &:hover {
                                cursor: pointer;
                                background-color: #ef2950;
                            }
                        }
                        .des-footer-down {
                            margin: 16px 0 0 0;
                            font-size: 12px;
                            text-align: center;
                            color: #a1a2a7;
                            a {
                                font-weight: bold;
                                color: #333;
                                text-decoration: none;
                                &:hover {
                                    text-decoration: underline;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}
@keyframes go-to-top {
    0% {
        transform: translateY(60%);
    }
    100% {
        transform: translateY(0);
    }
}
@keyframes go-to-top-back {
    0% {
        transform: translateY(0);
    }
    100% {
        transform: translateY(60%);
    }
}
@keyframes growth {
    0% {
        transform: scale(0.5);
    }
    100% {
        transform: scale(1);
    }
}
@keyframes smallll {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0);
    }
}
@media (max-width: 739px) {
    .download {
        .download-app {
            .download-app-children {
                .download-app-children-container {
                    width: 400px;
                    height: 566px;
                    overflow-y: auto;
                    &::-webkit-scrollbar {
                        display: none;
                    }
                    .download-app-content {
                        flex-direction: column;
                        .download-app-content-qr {
                            width: 400px;
                            .qr-title {
                                width: 100%;
                                margin: 24px 0;
                                font-weight: 500;
                            }
                        }
                        .space-down-app {
                            display: none;
                        }
                        .download-app-content-down {
                            .download-phonenumber {
                                display: none;
                            }
                            .footer-down {
                                display: none;
                            }
                        }
                    }
                }
            }
        }
    }
}
