.video {
    margin-top: 16px;
    padding-bottom: 20px;
    border-bottom: 1px solid #ccc;
    .video-container {
        display: flex;
        .video-avatar {
            width: 56px;
            height: 56px;
            margin-right: 14px;
            .video-avatar-img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
            }
        }
        .video-content {
            flex: 1;
            .video-content-header {
                display: flex;
                justify-content: space-between;
                align-items: center;
                .video-content-header-des {
                    .video-heading-name-acc {
                        display: flex;
                        align-items: center;

                        .video-content-heading {
                            font-size: 18px;
                            text-decoration: none;
                            color: #000;
                            font-weight: bold;
                            &:hover {
                                text-decoration: underline;
                            }
                            
                        }
                        .video-content-heading-active {
                            text-decoration: none;
                            color: #333;
                            margin-left: 4px;
                            font-weight: 400;
            
                        }
                    }
                    
                    .video-content-text {
                        margin: 6px 0;
                        span {
                            font-weight: bold;
                            &:hover {
                                cursor: pointer;
                                text-decoration: underline;
                            }
                        }
                    }
                    .video-content-link {
                        text-decoration: none;
                        font-weight: 500;
                        color: #000;
                        display: flex;
                        align-items: center;
                        span {
                            margin-right: 8px;
                            position: relative;
                            top: 4px;
                        }
                        p {
                            margin: 0;
                            &:hover {
                                cursor: pointer;
                                text-decoration: underline;
                            }
                        }
                    }
                }
                .video-content-header-btn {
                    .video-content-header-btn-follow {
                        min-width: 90px;
                        border: 1px solid;
                        font-size: 16px;
                        color: #fe2c55;
                        padding: 4px 0;
                        font-weight: 500;
                        border-radius: 4px;
                        background-color: #fff;
                        &:hover {
                            background-color: #fff3f5;
                            cursor: pointer;
                        }
                    }
                }
            }
            .video-content-body {
                margin-top: 16px;
                display: flex;
                .content-body-video-play {
                    width: 280px;
                    height: 498px;
                    position: relative;
                    border-radius: 8px;
                    overflow: hidden;
                    .watch-video {
                        width: 100%;
                        height: 100%;
                    }
                    .controls {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        position: absolute;
                        bottom: 24px;
                        right: 0;
                        left: 0;
                        padding: 0 20px;
                        color: #fff;
                        .action-video {
                            .play {
                                font-size: 24px;
                            }
                            .pause {
                                font-size: 24px;
                                display: none;
                            }
                        }
                        .volums {
                            input[type=range] {
                                transform: rotate3d(1, 0, 15, -90deg);
                                height: 10px;
                                width: 66px;
                                position: relative;
                                top: -52px;
                                left: 44px;
                                &::-webkit-slider-thumb {
                                    -webkit-appearance: none;
                                    appearance: none;
                                    width: 25px;
                                    height: 25px;
                                    border-radius: 50%;
                                    background: red;
                                    cursor: pointer;
                                }
                            }
                            .volum-up {
                                font-size: 22px;
                            }
                            .volum-off {
                                font-size: 22px;
                                display: none;
                            }
                        }
                    }
                }
                .content-body-actions {
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-end;
                    margin-left: 24px;
                    .body-action {
                        text-align: center;
                        .action-link {
                            text-decoration: none;
                            color: #000;
                            display: block;
                            width: 48px;
                            height: 48px;
                            background-color: #f1f1f2;
                            text-align: center;
                            line-height: 48px;
                            border-radius: 50%;
                            font-size: 20px;
                            &.active {
                                color: #fe2c55;
                                background-color: #fff3f5;
                            }
                            &.big-size {
                                font-size: 28px;
                            }
                            &:hover {
                                background-color: #e7e7e9;
                            }
                        }
                        .action-text {
                            color: #50525b;
                            margin: 8px 0;
                            font-size: 14px;
                            font-weight: bold;
                        }
                    }
                }
            }
        }
    }
}
@media (max-width: 1023px) {
    .video {

        .video-container {
            justify-content: center;
            .video-content {
                flex: 1;
            }
        }
    }
}
